import * as React from 'react';

const renderIcon = (icon) => {
  switch (icon) {
    case 'protection':
      return <div>protection</div>;
    case 'insurance':
      return <div>insurance</div>;
  }
};

export const HostConfidenceCard = ({ icon, title, description }) => {
  return (
    <div className='w-full lg:w-1/2'>
      <div className='w-90p'>
        <div>{renderIcon(icon)}</div>
        <div>
          <h3
            style={{ fontFamily: 'airbnb-bold' }}
            className='leading-9 text-gray-750'>
            {title}
          </h3>
        </div>
        <div>
          <p style={{ fontFamily: 'airbnb-book' }} className='text-gray-750'>
            {description}
          </p>
        </div>
        <div
          style={{ fontFamily: 'airbnb-book' }}
          className='my-6 text-green-850'>
          <a href=''>Learn more about Airbnb's Host Guarantee</a>
        </div>
      </div>
    </div>
  );
};
